<template>
    <div>
    <v-data-table
      :headers="selectedHeaders"
      :items="showData"
      :loading="bLoading"
      :search="search"
      loading-text= "Loading... Please wait"
      class="elevation-24"
    >
      <template v-slot:top>
        <v-row align="center">
          <v-col cols="12" sm="1" md="1" align = "center"> 
            <h3> Profiles </h3>
          </v-col>
          <v-divider class = "grey lighten-2 my-6" inset vertical></v-divider>
          <v-col cols="12" sm="5" md="5" align = "left"> 
            <v-select v-model="selectedHeaders" :items="headers" label="show columns" multiple return-object>
              <template v-slot:selection="{ item, index }">
                <v-chip v-if="index < 5">
                  <span>{{ item.text }}</span>
                </v-chip>
                  <span v-if="index === 5" class="grey--text caption">(+{{ selectedHeaders.length - 5 }} others)</span>
              </template>
            </v-select>
          </v-col>
          <v-divider class = "grey lighten-2 my-6" inset vertical></v-divider>
          <v-col cols="12" sm="4" md="4" align = "left">
            <v-text-field v-model="search" class = "mb-3" label="search" append-icon="mdi-text-search" single-line hide-details auto></v-text-field>
          </v-col>
          <v-divider class = "grey lighten-2 my-6" inset vertical></v-divider>
          <v-col cols="12" sm="2" md="2" align = "center">
          <v-dialog v-model="dialog" max-width="600px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn class="mb-2 white--text" v-bind="attrs" v-on="on" :color="getColor">New Item</v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="12" md="12">
                      <v-text-field
                        required
                        v-model="editedItem.name"
                        label="Name"
                        :rules="[() => editedItem.name.length > 0 || 'Required field']"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                      <v-text-field
                        v-model="editedItem.username"
                        label="Username"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                      <v-text-field
                        v-model="editedItem.password"
                        label="Password"
                        :type="editedItem.showPassword ? 'text' : 'password'"
                        :append-icon="editedItem.showPassword ? 'mdi-eye-off' : 'mdi-eye'"
                        @click:append="editedItem.showPassword = !editedItem.showPassword"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                      <v-text-field
                        v-model="editedItem.gateway"
                        label="Gateway"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                      <v-text-field
                        v-model="editedItem.enterprise"
                        label="Enterprise"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="12" md="12">
                      <v-text-field
                        v-model="editedItem.srvrMgrPath"
                        label="Server Manager Path"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="4" md="4">
                      <v-text-field
                        v-model="editedItem.zookeeper.host"
                        label="ZooKeeper Host"
                      ></v-text-field>
                    </v-col>
                    
                    <v-col cols="12" sm="4" md="4">
                      <v-text-field
                        v-model="editedItem.zookeeper.username"
                        label="ZooKeeper Username"
                      ></v-text-field>
                    </v-col>

                    <v-col cols="12" sm="4" md="4">
                      <v-text-field
                        v-model="editedItem.zookeeper.password"
                        label="ZooKeeper Password"
                        :type="editedItem.showZKPassword ? 'text' : 'password'"
                        :append-icon="editedItem.showZKPassword ? 'mdi-eye-off' : 'mdi-eye'"
                        @click:append="editedItem.showZKPassword = !editedItem.showZKPassword"
                      ></v-text-field>

                    </v-col>             
                    <v-col cols="12" sm="6" md="6">
                        <v-select
                          v-model="editedItem.color"
                          :items="colorSelector"
                          label="Select color"
                          return-object
                          solo
                          outlined
                          dense
                        />
                    </v-col>
                    <v-col cols="12" sm="6" md="6">
                      <v-checkbox
                        v-model="editedItem.readonly"
                        label="Read-only"
                      ></v-checkbox>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          </v-col>
          <v-dialog v-model="dialogDelete" max-width="600px">
            <v-card>
              <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
        <v-divider class = "grey lighten-2" horizontal></v-divider>
      </template>
      <template v-slot:[`item.readonly`]="{ item }">
         <div class="d-flex justify-center">
          <v-checkbox
            v-model="item.readonly"
            disabled
          ></v-checkbox>
         </div>
      </template>

      <template v-slot:[`item.password`]="{ item }">
        <v-text-field
          readonly
          class="shrink"
          v-model="item.password"
          :type="item.showPassword ? 'text' : 'password'"
          :append-icon="item.showPassword ? 'mdi-eye-off' : 'mdi-eye'"
          @click:append="item.showPassword = !item.showPassword"
        ></v-text-field>
      </template>

      <template v-slot:[`item.zookeeper`]="{ item }">
          <v-list dense>
            <v-list-item 
              v-for="(value, name) in item.zookeeper"
              :key="value"
            >
               <div v-if="name != 'password'">{{ name}} : {{value}}</div>
               <div v-if="name == 'password'">{{ name}} : *****</div>
            </v-list-item>
          </v-list>
      </template>

      <template v-slot:[`item.actions`]="{ item }">
        <v-icon
          small
          class="mr-2"
          @click="editItem(item)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          @click="deleteItem(item)"
        >
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:no-data>
        <v-btn
          color="primary"
          @click="getAllData"
        >
          Reset
        </v-btn>
      </template>
    </v-data-table>
    </div>
</template>
<script>

import ProfilesDataService from "../services/ProfilesDataService";
  export default {
   data: () => ({
      search: '',
      dialog: false,
      dialogDelete: false,
      headers: [
        { text: 'Name', align: 'start', value: 'name'},
        { text: 'Username', value: 'username' },
        { text: 'Password', width: '10%',value: 'password' },
        { text: 'Server Manager Path', value: 'srvrMgrPath' },
        { text: 'Gateway', value: 'gateway' },
        { text: 'Enterprise', value: 'enterprise' },
        { text: 'Color', value: 'color' },
        { text: 'ZooKeeper', width: '15%',value: 'zookeeper' },
        { text: 'Read-only', width: '5%', value: 'readonly' },
        { text: 'Actions', width: '5%', align: 'center', value: 'actions', sortable: false, groupable: false, },
      ],
      selectedHeaders: [
        { text: 'Name', align: 'start', value: 'name'},
        { text: 'Username', value: 'username' },
        { text: 'Password',  width: '10%', value: 'password' },
        { text: 'Server Manager Path', value: 'srvrMgrPath' },
        { text: 'Gateway', value: 'gateway' },
        { text: 'Enterprise', value: 'enterprise' },
        { text: 'Color', value: 'color' },
        { text: 'Read-only', width: '5%', value: 'readonly' },
        { text: 'Actions', width: '5%', align: 'center', value: 'actions', sortable: false, groupable: false, },
      ],
      colorSelector: ["red", "pink", "purple", "deep-purple", "indigo", "blue", "light-blue", "cyan", "teal", "green", "light-green", "lime", "yellow", "amber", "orange", "deep-orange", "brown", "blue-grey", "grey"],
      inputData: [],
      editedIndex: -1,
      editedItem: {
        name: '',
        username: '',
        password: '',
        srvrMgrPath: '',
        gateway: '',
        enterprise: '',
        color: '',
        readonly: null,
        showPassword: false,
        showZKPassword: false,
        zookeeper: {
          host: '',
          password: '',
          username: ''
        },
      },
      defaultItem: {
        name: '',
        username: '',
        password: '',
        srvrMgrPath: '',
        gateway: '',
        enterprise: '',
        color: '',
        readonly: null,
        showPassword: false,
        showZKPassword: false,
        zookeeper: {
          host: '',
          password: '',
          username: ''
        },
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
      bLoading () {
        if (this.inputData.length > 0) {return false;}
        else {return true;}
      },
      showData: {
        get () {
          return this.inputData
        },
      },
      getColor: {
        get () {
          return (this.$store.state.selectedTemplate.color) ? this.$store.state.selectedTemplate.color : 'primary'
        },
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
      value(val) {
        this.selectedHeaders = val;
      },
    },

    created () {
      //
    },
    
    mounted() {
      this.getAllData()
    },

    methods: {
      // 1. GET Data        
      async getAllData () {
        try {
          const response = await ProfilesDataService.getAll()
          this.inputData = this.dataTranform(response.data)
        } catch(e) {
          console.log(e);
        }
      },
      
      // 2. POST Data        
      async createData (data) {
        try {
          const response = await ProfilesDataService.create(data)
          return response
        } catch(e) {
          console.log(e);
        }
      },
      // 3. DELETE Data
      async deleteData (name) {
        try {
          const response = await ProfilesDataService.delete(name)
          return response
        } catch(e) {
          console.log(e);
        }
      },

      dataTranform (input) {
        var result = [];
        for (let parent in input) {
          input[parent].name = parent
          input[parent].showPassword = false
          input[parent].showZKPassword = false
          result.push(input[parent]);
        }
        return result
      },

      editItem (item) {
        this.editedIndex = this.inputData.indexOf(item);
        this.editedItem = Object.assign({}, item);    
        this.dialog = true;
      },

      deleteItem (item) {
        this.editedIndex = this.inputData.indexOf(item);
        this.editedItem = Object.assign({}, item);
        this.dialogDelete = true;
      },

      deleteItemConfirm () {
        this.deleteData (this.inputData[this.editedIndex].name);
        this.inputData.splice(this.editedIndex, 1);
        this.closeDelete();
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        delete this.editedItem.showPassword;
        delete this.editedItem.showZKPassword;
        if (this.editedIndex > -1) {
          this.createData (this.editedItem);
          Object.assign(this.inputData[this.editedIndex], this.editedItem);
          this.$store.dispatch('setTemplate', this.editedItem)
        } else {
          this.createData (this.editedItem);
          this.inputData.push(this.editedItem);
        }
        this.close()
      },
    },
  }
</script>